import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { HomeComponent } from './components/home/home.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { ProfileComponent } from './components/profile/profile.component';
import { ContactComponent } from './components/contact/contact.component';
import { PricingComponent } from './components/pricing/pricing.component';
import { PlatinumplanComponent } from './components/platinumplan/platinumplan.component';
import { GoldplanComponent } from './components/goldplan/goldplan.component';
import { SilverplanComponent } from './components/silverplan/silverplan.component';
import { BronzeplanComponent } from './components/bronzeplan/bronzeplan.component';
import { CartComponent } from './components/cart/cart.component';
import { MessagesComponent } from './components/messages/messages.component';
import { SettingsComponent } from './components/settings/settings.component';
import { NotesComponent } from './components/notes/notes.component';
import { ApidocsComponent } from './components/apidocs/apidocs.component';
import { AccountShowComponent } from './components/account-show/account-show.component';
import { AccountShowIdComponent } from './components/account-show-id/account-show-id.component';
import { ContactformShowComponent } from './components/contactform-show/contactform-show.component';
import { ContactformShowIdComponent } from './components/contactform-show-id/contactform-show-id.component';
import { AccountCreateComponent } from './components/account-create/account-create.component';
import { ContactformCreateComponent } from './components/contactform-create/contactform-create.component';
import { AccountUpdateIdComponent } from './components/account-update-id/account-update-id.component';
import { AccountDeleteIdComponent } from './components/account-delete-id/account-delete-id.component';

import { ValidateService } from './services/validate.service';
import { AuthService } from './services/auth.service';
import { FlashMessagesModule } from 'angular2-flash-messages';
import { AuthGuard } from './guards/auth.guard';

const appRoutes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'login', component: LoginComponent},
  {path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard]},
  {path: 'profile', component: ProfileComponent, canActivate: [AuthGuard]},
  {path: 'contact', component: ContactComponent},
  {path: 'pricing', component: PricingComponent, canActivate: [AuthGuard]},
  {path: 'platinumplan', component: PlatinumplanComponent, canActivate: [AuthGuard]},
  {path: 'goldplan', component: GoldplanComponent, canActivate: [AuthGuard]},
  {path: 'silverplan', component: SilverplanComponent,canActivate: [AuthGuard]},
  {path: 'bronzeplan', component: BronzeplanComponent, canActivate: [AuthGuard]},
  {path: 'cart', component: CartComponent, canActivate: [AuthGuard]},
  {path: 'messages', component: MessagesComponent, canActivate: [AuthGuard]},
  {path: 'settings', component: SettingsComponent, canActivate: [AuthGuard]},
  {path: 'notes', component: NotesComponent, canActivate: [AuthGuard]},
  {path: 'apidocs', component: ApidocsComponent},
  {path: 'account-show', component: AccountShowComponent},
  {path: 'account-show-id', component: AccountShowIdComponent},
  {path: 'contactform-show', component: ContactformShowComponent},
  {path: 'contactform-show-id', component: ContactformShowIdComponent},
  {path: 'account-create', component: AccountCreateComponent},
  {path: 'contactform-create', component: ContactformCreateComponent},
  {path: 'account-update-id', component: AccountUpdateIdComponent},
  {path: 'account-delete-id', component: AccountDeleteIdComponent},
  {path: '**', component: HomeComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    RegisterComponent,
    HomeComponent,
    DashboardComponent,
    ProfileComponent,
    ContactComponent,
    PricingComponent,
    PlatinumplanComponent,
    GoldplanComponent,
    SilverplanComponent,
    BronzeplanComponent,
    CartComponent,
    MessagesComponent,
    SettingsComponent,
    NotesComponent,
    ApidocsComponent,
    AccountShowComponent,
    AccountShowIdComponent,
    ContactformShowComponent,
    ContactformShowIdComponent,
    AccountCreateComponent,
    ContactformCreateComponent,
    AccountUpdateIdComponent,
    AccountDeleteIdComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    FlashMessagesModule.forRoot()
  ],
  providers: [ValidateService, AuthService, AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }



